@import "../scss/colors.scss";
@import "../scss/Typography.scss";
@import "../scss/buttons.scss";

// Base structure
html,
body {
  color: #fff;
  height: 100%;
  padding: 0;
  margin: 0;
  background-color: $color-background-3;
  
}

$grid__bp-md: 1200;
.container {
  max-width: $grid__bp-md * 1px;
  margin: auto;
  padding: 10px;
}

.padding-50 {
  padding: 50px 0px;
}
.cover-heading {
  font-size: 2em;
  text-align: left;
}

.heading::before {
  content: "\2014 \00A0";
}

.background-1 {
  background-color: $color-background-1;
}
.background-2 {
  background-color: $color-background-2;
}
.background-3 {
  background-color: $color-background-3;
}
.background-4 {
  background-color: $color-background-4;
}

.cover-height {
  min-height: 100vh;
}

.cover-heading {
  padding-top: 2em;
}
.cover-name {
  font-family: $font-dosis;
  font-size: 5em;
  font-weight: 600;
  text-align: left;
}
.display {
  display: block !important;
}
.cover-image {
  display: block;
  width: 100%;
  position: relative;
  margin: auto;
}

.cover-text {
  font-size: 1.3em;
  font-weight: 400;
  text-align: left;
}
.color-yellow {
  color: $color-yellow;
}
.color-blue {
  color: $color-blue;
}
.vertical-center {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.vertical-bottom {
  position: absolute;
  bottom: 0;
}
p {
  font-family: $font-Open-Sans;
  font-size: 1.6em;
}

.blockquote-footer {
  font-family: $font-Open-Sans;
  font-size: 2em;
}

@media screen and (max-width: 599px) {
  .site-wrapper {
    margin: 1em;
  }
  .blockquote-footer {
    font-family: $font-Open-Sans;
    font-size: 1.3em;
  }
  .cover-name {
    font-family: $font-dosis;
    font-size: 3em;
    line-height: 1.2em;
    font-weight: 600;
  }
  .cover-text {
    font-size: 1.1em;
    font-weight: 400;
    line-height: 1.5em;
  }
  .cover-image {
    overflow: hidden;
    width: 90vw;
    position: relative;
    bottom: 0px;
  }

  p {
    font-family: $font-Open-Sans;
    font-size: 1.3em;
    line-height: 1.25em;
  }
}

.cover-avatar {
  display: none;
}
@media screen and (max-width: 599px) {
  .cover-avatar {
    display: inline;
    width: 95vw;
    // height: auto;
  }
  .cover-image {
    display: none;
  }
  .cover-heading {
    line-height: 0.2em;
  }
  .cover-text {
    display: none;
  }
}

.text-left {
  text-align: left;
}
.tag-text {
  font-family: $font-dosis;
  font-size: 2.5em;
  font-weight: 100;
}

.about-image {
  max-width: 400px;
  display: block;
  width: 100%;
  position: relative;
  margin: auto;
}

.about-heading {
  font-size: 3em;
  font-weight: 700;
  font-family: $font-dosis;
}

.about-text {
  font-family: $font-Open-Sans;
  font-size: 0.8em;
  color: $color-light-blue;
  font-weight: 100;
}
.about-focus {
  font-size: 2em;
  padding: 10px 0;
}

@media screen and (max-width: 599px) {
  .about-image {
    display: none;
  }
}

// Overides

p {
  display: block;
  margin-block-start: 0.7em;
  margin-block-end: 0.7em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
}

h2 {
  display: block;
  font-size: 1.5em;
  margin-block-start: 0.63em;
  margin-block-end: 0.63em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-weight: bold;
}
