// Disabled

.btn-default,
.btn-default:hover,
.btn-default:focus {
  font-family: $font-dosis;
  font-weight: 500;
  color: $color-black;
  background-color: $color-yellow;
  border: none;
  text-transform: uppercase;
  padding: 5px 25px;
  border-radius: 5px;
  font-size: 1.2em;
  text-align: center;
  text-decoration: none;
  margin-right: 1em;
}
.btn-default:hover {
  color: $color-white;
  background-color: $color-blue;
}

.btn-scroll {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  font-size: 8px;
  text-align: center;
  background-color: $color-yellow;
  z-index: 99;
  border: none;
}

.btn-link {
  color: inherit;
  font-family: inherit;
  text-decoration: underline dotted $color-yellow;
}
.btn-link:hover {
  color: $color-yellow;
  text-decoration: none;
}
